.admin-page-container{
  max-width:600px;
  overflow-x:none;
  margin:auto;
}

.admin-input-row-contianer{
  display:inline-block;
  margin: 5px 10px;
}

.input-tiny{
  width: 50px;
}

.input-medium{
  width:415px;
}

.input-large{
  width:505px;
}

.input-fill-space{
  display:block;
}

.image-upload-box{
  min-width:580px;
  min-heigth:200px;
  height:200px;
  background-color:silver;
  border: 1px dashed black;
  overflow: none;
}

.upload-preview-image-single{
  height:100%;
  width:auto;
  max-width:100%;
  display:block;
  margin:auto;
}

.upload-preview-image-multi{
  min-height:100%;
  max-height:100%;
  width:auto;
  max-width:200px;
  display:inline-block;
}
